.App {
  text-align: center;
}

body {
  margin: 40px;
}

#myname {
  font-size: 40px;
  font-weight: 700;
  font-family: "Raleway", sans-serif;
  display: inline;
  letter-spacing: default;
  margin: 0px;
}

#describeme {
  font-size: 40px;
  font-weight: 500;
  font-family: "Raleway", sans-serif;
  display: inline;
  letter-spacing: default;
  margin: 0px;
}

p {
  font-family: "Raleway", sans-serif;
}

.dinkus {
  font-size: 38px;
  margin: 0px;
}

a {
  color: #a50000;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

#header {
  background-color: white;
  color: black;
}

#portfolio h2 {
  text-align: left;
  font-weight: 500;
  font-style: italic;
  font-size: 24px;
}

#contact h2 {
  color: black;
  font-size: 24px;
  text-align: left;
  font-weight: 500;
}

#links {
  flex-direction: column;
  text-align: center;
  font-size: 15px;
  text-decoration: none;
  font-family: "Raleway", sans-serif;
  letter-spacing: 5px;
  text-transform: uppercase;
  padding: 0px 0px 20px 0px;
}

#main {
  background-color: white;
  text-align: center;
  font-family: "Raleway", sans-serif;
  color: black;
  padding-top: 20px;
  border: none;
}

#photograph img {
  display: block;
  max-width: 100%;
  height: auto;
  margin: auto;
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
}

@media screen and (min-width: 768px) {

  #myname,
  #describeme {
    font-size: 55px;
  }

  #contact h2 {
    font-size: 30px;
  }

  #portfolio h2 {
    font-size: 30px;
  }

  body {
    margin: 60px;
  }
}
